// Smooth Scrolling
navLinks.addEventListener("click", function (e) {
  e.preventDefault();

  if (e.target.classList.contains("nav__links--link")) {
    const id = e.target.getAttribute("href");
    document.querySelector(id).scrollIntoView({ behavior: "smooth" });
  }
});

// Make Navigation Fade animation

const navOpacity = function (e) {
  if (e.target.classList.contains("nav__links--link")) {
    const ourTarget = e.target;
    const siblings = ourTarget
      .closest(".nav")
      .querySelectorAll(".nav__links--link");
    const logo = ourTarget.closest(".nav").querySelector(".nav__logo-img");
    siblings.forEach((el) => {
      if (el != ourTarget) el.style.opacity = this;
    });
  }
};

navLinks.addEventListener("mouseover", navOpacity.bind(".5"));

navLinks.addEventListener("mouseout", navOpacity.bind("1"));

